import Vue from "vue";
import "./index.css";
import httputils from "@/js/httputils";

var _vue = new Vue({
  el: "#vueData",
  data: function () {
    return {
      expenseId: "",
      routeTypes: ["往返", "去程", "回程", "中间段"],
      trainSeats: [
        "(动车组)",
        "(硬座)",
        "(软座)",
        "(硬卧)",
        "(软卧)",
        "(高铁)",
        "(城际列车一等座)",
        "(城际列车二等座)",
        "火车"
      ],
      include: {
        Flight: {
          Datas: [{
            RouteType: "往返",
            Vehicle: "(动车组)"
          }],
          IsSelect: false
        },
        Train: {
          Datas: [{
            RouteType: "往返",
            Vehicle: "(动车组)"
          }],
          IsSelect: false
        },
        Bus: {
          Describe: "",
          IsSelect: false
        },
        Ship: {
          Describe: "",
          IsSelect: false
        },
        LocalCar: {
          Describe: "",
          IsSelect: false
        },
        TripHotel: false,
        Hotel: {
          Datas: [{
            Destination: "",
            Level: "",
            Room: "",
            Describe: ""
          }],
          IsSelect: false
        },
        SpecificHotel: {
          Datas: [""],
          IsSelect: false
        },
        HotelType: 0,
        Meal: {
          Breakfast: "",
          Dinner: "",
          People: "",
          Cost: "",
          IsSelect: false
        },
        MealOther: {
          Describe: "",
          IsSelect: false
        },
        Ticket: {
          Describe: "",
          IsSelect: false
        },
        Guide: {
          Type: 0,
          Local: "",
          Remark: ""
        },
        Shuttle: {
          Describe: "",
          IsSelect: false
        },
        Visa: {
          IsSelect: false
        },
        VisaMaterials: {
          Describe: "",
          IsSelect: false
        },
        Other: {
          Datas: [""],
          IsSelect: false
        },
        Intro: ""
      },
      exclude: {
        Local: false,
        Cost: false,
        Scenic: false,
        Product: false,
        Room: false,
        Insurance: false,
        OwnExpense: false,
        UnKnown: false,
        Hotel: false,
        LocalExpense: false,
        LiJiang: false,
        HaiNan: false,
        CustomsDuty: false,
        BorderTourInsurance: false,
        Visa: {
          Describe: "",
          IsSelect: false
        },
        Other: {
          Datas: [""],
          IsSelect: false
        },
        Intro: ""
      },
      child: {
        Age: {
          Min: "",
          Max: "",
          Describe: "",
          IsSelect: false
        },
        Height: {
          Min: "",
          Max: "",
          Describe: "",
          IsSelect: false
        },
        Remark: {
          Describe: "",
          IsSelect: false
        },
        Intro: ""
      }
    };
  },
  mounted: function () {
    this.initData();
  },
  methods: {
    initData: function () {
      var that = this;
      var loading = that.$loading({
        lock: true
      });
      httputils
        .get("/travel/supergoproduct/Explanation/GetExpense", {
          proid: $("#ProductId").val(),
          productType: $("#ProductType").val()
        })
        .then(function (res) {
          loading && loading.close();
          that.include = Object.assign(
            that.include,
            minux.checkBe(res.Result.Include)
          );
          that.exclude = Object.assign(
            that.exclude,
            minux.checkBe(res.Result.Exclude)
          );
          that.child = Object.assign(
            that.child,
            minux.checkBe(res.Result.Child)
          );
          that.expenseId = res.Result.Id;
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
          loading && loading.close();
        });
    },
    saveData: function () {
      let that = this;
      let loading = that.$loading({
        lock: true
      });
      httputils
        .post("/travel/supergoproduct/Explanation/SaveExpense", {
          expense: {
            Id: that.expenseId,
            Include: that.include,
            Exclude: that.exclude,
            Child: that.child
          },
          proId: $("#ProductId").val()
        })
        .then(result => {
          loading && loading.close();
          if (!result.IsSuccess) {
            that.$confirm(result.ErrorMsg, "保存失败", {
              confirmButtonText: "确定",
              type: "warning",
              center: true,
              showCancelButton: false
            });
          } else {
            that.$message({
              message: "产品费用说明保存成功！",
              type: "success"
            });
          }
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
          loading.close();
        });
    },
    addTraffic: function (type) {
      var that = this;
      var datas = that.includeFlight.Datas;
      if (type == 2) {
        datas = that.includeTrain.Datas;
      }
      datas.push({
        RouteType: "往返",
        Vehicle: "(动车组)"
      });
    },
    addHotel: function () {
      var that = this;
      that.includeHotel.Datas.push({
        Destination: "",
        Level: "",
        Describe: "",
        Room: ""
      });
    },
    add: function (obj) {
      if (!obj || !obj.Datas) obj.Datas = [];
      obj.Datas.push("");
    },
    remove: function (obj) {
      if (!obj || !obj.Datas) return;
      var length = obj.Datas.length;
      if (length <= 1) return;
      obj.Datas.splice(length - 1, 1);
    },
    removeByIndex: function (obj, index) {
      if (!obj || !obj.Datas) return;
      var length = obj.Datas.length;
      if (index >= length) return;
      if (length <= 1) return;
      obj.Datas.splice(index, 1);
    },
    NoneGuide: function () {
      var that = this;
      if (that.include && that.include.Guide) {
        that.include.Guide.Type = -1;
      }
    }
  },
  computed: {
    includeTrain: function () {
      var that = this;
      if (!that.include.Train) {
        that.include.Train = {
          IsSelect: false,
          Datas: [{
            RouteType: "往返",
            Vehicle: "(动车组)"
          }]
        };
      }
      if (!that.include.Train.Datas || that.include.Train.Datas.length <= 0) {
        that.include.Train.Datas = [{
          RouteType: "往返",
          Vehicle: "(动车组)"
        }];
      }
      return that.include.Train;
    },
    includeFlight: function () {
      var that = this;
      if (!that.include.Flight) {
        that.include.Flight = {
          IsSelect: false,
          Datas: [{
            RouteType: "往返",
            Vehicle: "(动车组)"
          }]
        };
      }
      if (!that.include.Flight.Datas || that.include.Flight.Datas.length <= 0) {
        that.include.Flight.Datas = [{
          RouteType: "往返",
          Vehicle: "(动车组)"
        }];
      }
      return that.include.Flight;
    },
    includeHotel: function () {
      var that = this;
      if (!that.include.Hotel) {
        that.include.Hotel = {
          IsSelect: false,
          Datas: [{
            Destination: "",
            Level: "",
            Describe: "",
            Room: ""
          }]
        };
      }
      if (!that.include.Hotel.Datas || that.include.Hotel.Datas.length <= 0) {
        that.include.Hotel.Datas = [{
          Destination: "",
          Level: "",
          Describe: "",
          Room: ""
        }];
      }
      return that.include.Hotel;
    },
    includeSpecificHotel: function () {
      var that = this;
      if (!that.include.SpecificHotel) {
        that.include.SpecificHotel = {
          IsSelect: false,
          Datas: [""]
        };
      }
      if (!that.include.SpecificHotel.Datas ||
        that.include.SpecificHotel.Datas.length <= 0
      ) {
        that.include.SpecificHotel.Datas = [""];
      }
      return that.include.SpecificHotel;
    },
    includeOther: function () {
      var that = this;
      if (!that.include.Other) {
        that.include.Other = {
          IsSelect: false,
          Datas: [""]
        };
      }
      if (!that.include.Other.Datas || that.include.Other.Datas.length <= 0) {
        that.include.Other.Datas = [""];
      }
      return that.include.Other;
    },
    excludeOther: function () {
      var that = this;
      if (!that.exclude.Other) {
        that.exclude.Other = {
          IsSelect: false,
          Datas: [""]
        };
      }
      if (!that.exclude.Other.Datas || that.exclude.Other.Datas.length <= 0) {
        that.exclude.Other.Datas = [""];
      }
      return that.exclude.Other;
    }
  }
});

$(function () {
  $("#saveProductInfo").click(function () {
    _vue.saveData();
  });
});



